/* eslint-disable */
import React from 'react';
import PropTypes from 'prop-types';
import Media from 'react-media';
import { connect } from "react-redux";
import { Link } from 'react-router-dom';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import styles from './LandingPageUIStyles';
import { openRegisterPageActionCreator } from '../../reducers/auth/authActionCreators';
import AuthModalContainer from '../../components/auth/AuthModalContainer';
import { getAuthInfoSelector } from '../../reducers/rootReducer';
/* eslint-enable */

/* order them from smallest to biggest */
const MEDIA_SCALE = {
  SMALL: {
    query: {
      maxWidth: 830,
    },
    scale: pixels => pixels
  },
  MEDIUM: {
    query: [{
      maxWidth: 1860,
      minWidth: 830,
    }],
    scale: pixels => `${pixels * 2}px`
  }
};

const ScalableLandingView = ({
  classes, scale, register, authInfo
}) => (
  <Paper className={classes.root}>
        <Typography
          style={{ fontSize: scale(styles.display1.fontSize) }}
          variant="display1"
          color="inherit"
          component="h1"
        >
          Rebind
        </Typography>
        <Typography
          style={{ fontSize: scale(styles.display2.fontSize) }}
          variant="display2"
          color="inherit"
          component="p"
        >
    is a project that brings the world closer together by making it simple to connect with anyone.
<br />This is achieved by empowering the users to present themselves to everyone with just one word.
<br />It is a cloud electronic contact book that is tailored to the natural way of human networking.
        </Typography>
        <Link to="/register">
        <Button
          style={{
            fontSize: scale(styles.button.fontSize),
            height: scale(styles.button.height),
            width: scale(styles.button.width)
          }}
          size="medium"
          variant="contained"
          color="primary"
          className={classes.button}
          onClick={() => register()}
        >
          <GroupAddIcon
            color="secondary"
            className={classes.icon}
            style={{
              fontSize: scale(styles.icon.fontSize),
            }}
          />
          Register
        </Button>

        </Link>
      </Paper>
);

ScalableLandingView.propTypes = {
  authInfo: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired,
  register: PropTypes.func.isRequired,
  scale: PropTypes.func.isRequired
};

const LandingPageUI = ({ classes, authInfo, openRegisterPage }) => (
  <div>
    <Media query={MEDIA_SCALE.SMALL.query} >
      <ScalableLandingView
        classes={classes}
        scale={MEDIA_SCALE.SMALL.scale}
        register={openRegisterPage}
      />
    </Media>
    <Media query={MEDIA_SCALE.MEDIUM.query}>
      <ScalableLandingView
        classes={classes}
        scale={MEDIA_SCALE.MEDIUM.scale}
        register={openRegisterPage}
      />
    </Media>
    <AuthModalContainer open={authInfo.isRegistering}/>
  </div>
);

LandingPageUI.propTypes = {
  authInfo: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired,
  openRegisterPage: PropTypes.func.isRequired,
};

const StyledLandingPageUI = withStyles(styles)(LandingPageUI);

const mapStateToProps = state => ({
  authInfo: getAuthInfoSelector(state)
});
const mapDispatchToProps = dispatch => ({
  openRegisterPage: () => dispatch(openRegisterPageActionCreator())
});
const ReduxLandingPageUI = connect(
  mapStateToProps,
  mapDispatchToProps
)(StyledLandingPageUI);

export default ReduxLandingPageUI;
